import Vue from 'vue';
import axios from 'axios';
import qs from 'qs';

// const AJAXURL = process.env.NODE_ENV === 'development' ? '/api' :  document.URL.startsWith('http')
//     ? ''
//     : process.env.API_ROOT;

const AJAXURL = process.env.NODE_ENV === 'development' ? '/api' : '';

//k扩展的请求的方法
Vue.prototype.$ajax = function(url, data, callback, type = 'post') {
    let self = this;
    let options = {};
    if (type === 'get') {
        data = JSON.stringify(data) === '{}' ? '' : '?' + qs.stringify(data);
        options = {
            method: type,
            url: AJAXURL + url + data,
        };
    } else if(type === 'post') {
        options = {
            method: type,
            url: AJAXURL + url,
            data: data,
        };
    }

    axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';
    axios(options).then(function(response) {
        console.log(response);
        // if (response.data.status_code !== 200) {
        //     // self.$Toast('接口报错');
        //     console.log('error:' + response.data.status_code);
        // }
        // if (callback) {
        //     callback(response.data);
        //     // console.log(data);
        // }
    }).catch(function(err) {
        console.log(err);
        // if (error) {error(err);}
    });
}



