(function (doc, win) {
    let systemFont = doc.documentElement.currentStyle ? doc.documentElement.currentStyle['fontSize'] : getComputedStyle(doc.documentElement,false)['fontSize'];

    let xs = parseFloat(systemFont) / 16;

    let docEl = doc.documentElement;
    let isIOS = navigator.userAgent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
    let dpr = isIOS ? Math.min(win.devicePixelRatio, 3) : 1;
    dpr = window.top === window.self ? dpr : 1;
    let resizeEvt = 'orientationchange' in window ?
        'orientationchange' :
        'resize';
    docEl.dataset.dpr = dpr;
    let recalc = function () {
        let width = docEl.clientWidth;

        if (width / dpr > 750) {
            width = 750 * dpr;
        }
        getDataset(docEl).width = width;
        getDataset(docEl).percent = 100 * (width / 750);

        docEl.style.fontSize = (100 * (width / 750)) / xs + 'px';
    };
    recalc();
    if (!doc.addEventListener) return;
    win.addEventListener(resizeEvt, recalc, false);

    function getDataset(ele) {
        if (ele.dataset) {
            return ele.dataset;
        } else {
            var attrs = ele.attributes,
                dataset = {},
                name,
                matchStr;

            for (var i = 0; i < attrs.length; i++) {
                matchStr = attrs[i].name.match(/^data-(.+)/);
                if (matchStr) {
                    name = matchStr[1].replace(/-([\da-z])/gi, function (all, letter) {
                        return letter.toUpperCase();
                    });
                    dataset[name] = attrs[i].value;
                }
            }
            return dataset;
        }
    }
})(document, window);



